import { OpenAPIRoute } from '@cloudflare/itty-router-openapi';

interface Env {
  TEST_QUEUE_BINDING: Queue<any>;
}

export class QueueTestHandler extends OpenAPIRoute {
  async handle(
    request: Request,
    env: any,
    context: any,
    data: Record<string, any>
  ) {
    console.log('Handler started!');
    try {
      await env.TEST_QUEUE_BINDING.send('Hello, world!');
    } catch (e) {
      console.log('Error writing to queue!');
      console.error(e);
      return new Response('Error writing to queue!', { status: 500 });
    }
    return new Response('Success!');
  }
}
